.skin-blue textarea,
.skin-blue .form textarea {
  font-family: "Microsoft YaHei";
  width: 100%;
  line-height: 18px;
  padding: 6px 10px;
  border: 1px solid #d9dee2;
  border-radius: 2px;
  font-size: 12px;
  color: #333333;
}

.tips {
  line-height: 18px;
  margin: 5px;
}

.edui-default .edui-toolbar {
  line-height: 18px;
}

.textlimit {
  margin: 0 -15px;
}

[v-cloak] {
  display: none;
}
